import { WebPlugin } from '@capacitor/core';

import type { ExifGalleryPlugin, InitConfig, PickOptions, PickResult } from './definitions';

export class ExifGalleryPluginWeb extends WebPlugin implements ExifGalleryPlugin {
  async initialize(config?: InitConfig): Promise<void> {
    console.log('ExifGallery.initialize() called on web', config);
    // Web implementation not supported
    throw new Error('ExifGallery plugin is not supported on web platform');
  }

  async pick(options?: PickOptions): Promise<PickResult> {
    console.log('ExifGallery.pick() called on web', options);
    // Web implementation not supported
    throw new Error('ExifGallery plugin is not supported on web platform');
  }
}
